body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.SearchResult {
    width: 900px;
    padding: 16px;
    text-align: left;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    margin: 10px;
    box-sizing: border-box;
    cursor: pointer;
    font-size: 80%;
}

.SearchResult:hover,
.SearchResult:active {
    background-color: #C0DDF5;
}

.Author {
    margin: 16px 0;
    color: rgb(105, 105, 105);
}

.Decription {
    margin: 16px 0;
    color: rgb(51, 51, 51);
}
.Posts {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: center;
            justify-content: center;
    width: 80%;
    margin: auto;
}

.SearchPage {
    width: 80%;
    margin: 20px auto;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    text-align: center;
}

.SearchPage label {
    display: block;
    margin: 10px auto;
    text-align: center;
    font-weight: bold;
}

.SearchPage input,
.SearchPage textarea,
.SearchPage select {
    display: block;
    width: 80%;
    box-sizing: border-box;
    border: 1px solid black;
    outline: none;
    font: inherit;
    margin: auto;
}

.SearchPage button {
    margin: 5px 0;
    padding: 10px;
    font: inherit;
    border: 1px solid #fa923f;
    background-color: transparent;
    color: #fa923f;
    cursor: pointer;
}

.SearchPage button:hover,
.SearchPage button:active {
    color: white;
    background-color: #fa923f;
}
